import React, { Component } from 'react';
import './App.css';
import { BrowserRouter, HashRouter, Route, Switch } from 'react-router-dom';
import store from './redux'
import { Provider } from 'react-redux';
// import Content from './Components/content'
import Nav from './Components/navbar';
// import Footer from './Components/footer'
import Login from './Components/login'
import Signup from './Components/signup'
import Home from './Components/home'
import PostProduct from './Components/postproduct'
import Addetails from './Components/addetails'
import Profile from './Components/profile'
import Search from './Components/search'
import UserAds from './Components/userads'
import UpdateAd from './Components/updatead'
// import history from './Components/history'
class App extends Component {
   
   componentDidMount(){
    fetch("http://localhost:8000/dashboard")
    .then((res)=>res.text())
    .then((data)=>{
      console.log(data)
    })  
    .catch((err)=>{console.log(err)})

   }

  render() {
    return (
      <Provider store={store}>

        <HashRouter >
          <div style={{minWidth:"300px"}} >
            <Nav  />

              {/* <MobileForm/> */}
            <Switch>
              <Route path='/login' component={Login} />
              <Route path='/signup' component={Signup} />
              <Route path='/updatead/:id' component={UpdateAd} />
              <Route path='/ad-details/:id/:userID' component={Addetails} />
              <Route path='/profile' component={Profile} />
              <Route path='/search' component={Search} />
              <Route path='/sell' component={PostProduct} />
              <Route path='/userads' component={UserAds} />

              <Route path="/" component={Home} />

              {/* <Route path='/TDetails' component={TDetails} /> */}
              {/* <Route path='/newtransaction' component={NewTransaction} /> */}
              {/* <Route path='/withdraw' component={Withdraw} /> */}
              {/* <Route exact path='/' component={Dashboard} /> */}

              {/* <Accounts />
                <Transactions /> */}
            </Switch>

            {/* <Footer /> */}
          </div>
        </HashRouter>
      </Provider>

    );
  }
}


export default  App;
